<template>
	<view class="container" v-if="loading"> 
		<view class="tips">温馨提示：如发现工程师中途无故加价，请拒绝付款！</view>
		<view class="box">
			<view class="box-t">
				<view class="title">预约信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cell-box">
					<view class="cell-box-item">
						<view class="left">预约时间</view>
						<view class="right">{{model.yuyuedate}} {{model.yuyuetime}}</view>
					</view>
					<view class="cell-box-item">
						<view class="left">服务地址</view>
						<view class="right">
							{{address.address_details}}
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="box"  v-if="model.status>1">
			<view class="box-t">
				<view class="title">师傅信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cell-box">
					<view class="cell-box-item">
						<view class="left">姓名</view>
						<view class="right">
							{{model.teacher_name}}
						</view>
					</view>
					<view class="cell-box-item">
						<view class="left">电话</view>
						<view class="right">{{model.teacher_mobile}} <text style="padding: 0 15rpx;">|</text> <text
								class="blue" @click="makecall(model.teacher_mobile)">拨打</text></view>
					</view>
				</view>
			</view>
		</view>
		<view class="box">
			<view class="box-t">
				<view class="title">服务信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cart-lists">
					<view class="item">
						<image :src="model.service_thumb" mode="scaleToFill" class="thumb">
						</image>
						<view class="info">
							<view class="title">
								{{model.service_name}}
								<text>x1</text>
							</view>
							<view class="num">￥{{model.price}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="box">
			<view class="box-t">
				<view class="title">订单信息</view>
				<view class="more"></view>
			</view>
			<view class="box-c">
				<view class="cell-box">
					<view class="cell-box-item">
						<view class="left">订单号码</view>
						<view class="right">{{model.orderid}} <text style="padding: 0 15rpx;">|</text> <text
								class="blue" @click="copy(model.orderid)">复制</text></view>
					</view>
					<view class="cell-box-item">
						<view class="left">下单时间</view>
						<view class="right">
							{{model.created_at}}
						</view>
					</view>
				</view>

			</view>
		</view>

		<view class="box" v-if="model.status<5">
			<view class="box-t">
				<view class="title">操作订单</view>
				<view class="more"></view>
			</view>
			<view class="box-btns" v-if="model.status==1">
				<view class="btn plain" @click="cancelOrder">取消订单</view>		 
			</view>
			<view class="box-btns" v-if="model.status==3">		 		 
				<view class="btn" @click="getHeding">支付核定</view>
			</view>
			<view class="box-btns" v-if="model.status==4">		
				<view class="btn" @click="$refs.comment.open()">我要评价</view>			
			</view>
		</view>
		
		<view style="height: 20rpx;"></view>
		<uni-popup ref="comment">
			<view class="comment-box">
				<view class="title">我要评价</view>
				<view class="star">
					<uni-rate v-model="comment.star" color="#bbb" active-color="red" />
				</view>
				<view class="textarea">
					<uni-easyinput v-model="comment.content" placeholder="请输入评论内容" type="textarea"></uni-easyinput>
				</view>
				<view class="upload">
					<uni-file-picker v-model="comment.thumbs" fileMediatype="image" mode="grid" file-extname="png,jpg"
						:limit="3" @select="select" />
				</view>
				<view class="btn" @click="saveComment">我要评价</view>
			</view>
		</uni-popup>
		<uni-popup ref="heding">
			<view class="popup">
				<view class="popup-title">维修说明</view>
				<view class="dl">
					<view class="dt">保修到期</view>
					<view class="dd">{{heding.baoxiu}}</view>
				</view>
				<view class="dl">
					<view class="dt">总金额</view>
					<view class="dd">
						{{heding.price}}元
					</view>
				</view>
				<view class="dl">
					<view class="dt">备注信息</view>
					<view class="dd">{{heding.content}}</view>
				</view>
				<view class="dl">
					<view class="dt">现场图片</view>
					<view class="dd">
						<view class="pictures">
							<view class="item" v-for="(item,index) in heding.pictures" @click="preview(i)">
								<image :src="item.url"></image>
							</view>
						</view>
					</view>
				</view>
				<!-- <view class="info">
					<uni-icons type="info-filled" color="#2F80FF"></uni-icons> 下单后可以修改配送信息
				</view> -->
				<view class="btn" @click="pay">确定支付</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: 0,
				model: {},
				address: {},
				comment: {
					order_id: 0,
					uid: 0,
					star: 5,
					content: '',
					thumbs: [],
				},
				heding: {},
				loading:false
			}
		},
		onLoad(option) {
			this.id = option.id
			this.getContent()
		},
		methods: {
			async getContent() {
				let res = await this.$u.get('/v2/order/show', {
					uid: this.vuex_user.id,
					id: this.id
				})
				this.model = res.data.model
				this.address = res.data.address
				this.loading = true
				console.log(res.data)
			},
			copy(orderid) {
				uni.setClipboardData({
					data: orderid, //要被复制的内容
					success: () => { //复制成功的回调函数
						uni.showToast({ //提示
							title: '复制成功'
						})
					}
				});
			},
			async cancelOrder() {
				uni.showModal({
					title: '提示',
					content: '确定要取消这个订单吗？',
					success: (res) => {
						if (res.confirm) {
							//取消订单
							this.$u.get('/v2/order/cancel', {
								uid: this.vuex_user.id,
								id: this.id
							}).then((res) => {
								if (res.code == 200) {
									this.$u.toast('取消成功')
									uni.navigateBack({
										delta: 1
									})
								}
							})
						} else if (res.cancel) {

						}
					}
				});
			},
			async commentOrder() {
				//评论订单
				let res = await this.$u.get('/v2/order/cancel', {
					uid: this.vuex_user.id,
					id: this.id
				})
				if (res.code == 200) {
					this.$u.toast('取消成功')
					uni.navigateBack({
						delta: 1
					})
				} else {
					this.$u.toast('取消失败')
				}
			},
			select(e) {
				const tempFilePaths = e.tempFilePaths;
				tempFilePaths.forEach((item) => {
					this.upload(item)
				})
			},
			upload(file) {
				uni.uploadFile({
					url: this.$baseUrl() + '/v2/file/images',
					filePath: file,
					name: 'file',
					success: (res) => {
						let data = JSON.parse(res.data)
						this.comment.thumbs.push({
							"name": data.data.name,
							"extname": data.data.extension,
							"url": data.data.url
						})
					},
					complete: (res) => {
						console.log(res);
					}
				});
			},
			async saveComment() {
				this.comment.order_id = this.id
				this.comment.uid = this.vuex_user.id
				let res = await this.$u.post('/v2/comment/add', this.comment)
				if (res.code == 200) {
					this.$u.toast('评论成功')
					this.$refs.comment.close()
					this.comment = {
						order_id: 0,
						uid: 0,
						star: 5,
						content: '',
						thumbs: [],
					}
				}
			},
			async getHeding() {
				let res = await this.$u.get('/v2/order/heding', {
					id: this.id
				})
				if (res.code == 200) {
					this.heding = res.data
					this.$refs.heding.open()
				} else {
					this.$u.toast(res.message)
				}
			},
			preview(index) {
				let  urls = this.heding.pictures.map((item)=>{
					return item.url
				})
				uni.previewImage({
					current:index,
					urls: urls,
					longPressActions: {
						success: function(data) {
							console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
						},
						fail: function(err) {
							console.log(err.errMsg);
						}
					}
				});
			},
			async pay() {
				let res = await this.$u.post('/v2/order/payall', {
					order_id: this.heding.id,
					uid: this.model.uid
				})
				if (res.code != 200) {
					this.$u.toast(res.message)
					return
				}
				//this.$u.toast(res.message)
				let config = res.data.config
				let that = this
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: config.timestamp,
					nonceStr: config.nonceStr,
					package: config.package,
					signType: config.signType,
					paySign: config.paySign,
					success: function() {
						that.$refs.heding.close()
						that.$u.toast('支付成功')
						that.getContent()
					},
					fail: function(err) {
						console.log('fail:' + JSON.stringify(err));
						that.$refs.heding.close()
						that.$u.toast('支付失败')
			
					}
				});
			},
			makecall(phone){
				uni.makePhoneCall({
					phoneNumber:phone
				})
			}

		}
	}
</script>

<style scoped>
	.container {
		display: block;
		min-height: 100vh;
		background-color: #F8F8F9;
		position: relative;
	}
	.tips{
		background-color: rgba(255, 68, 0, 0.1);
		height: 100rpx;
		font-size: 28rpx;
		font-weight: 500;
		color: #FF4400;
		display: flex;
		align-items: center;
		padding:0 20rpx
	}
 
	.box {
		border-radius: 20rpx;
		box-shadow: 0 0 20rpx rgba(0, 0, 0, 0.1);
		margin: 20rpx 20rpx 0 20rpx;
		background-color: #fff;
	}

	.box .box-t {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx;
	}

	.box .box-t .title {
		font-size: 32rpx;
		font-weight: bold;
	}

	.box .box-t .title .text {
		color: #777
	}

	.box .box-t .more {
		font-size: 24rpx;
		color: #F65425
	}

	.box .box-btns {
		padding: 20rpx;
		display: flex;
		justify-content: space-around;
	}

	.box .box-btns .btn {
		border-radius: 40rpx;
		background-color: #FF4400;;
		color: #fff;
		width: 180rpx;
		height: 64rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 28rpx;
	}

	.box .box-btns .btn.plain {
		background-color: #fff;
		color: #FF4400;
		border: #FF4400 1px solid;
	}

	.cell-box {
		margin: 0 20rpx;
	}

	.cell-box .cell-box-item {
		border-top: #F8F8F8 1px solid;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 72rpx;
		padding: 10rpx 0;
		font-size: 28rpx;
	}

	.cell-box .cell-box-item .left {
		color: #999999;
		width: 160rpx;
	}

	.cell-box .cell-box-item .right {
		flex: 1;
		text-align: right;
	}

	.cell-box .cell-box-item .right .blue {
		color: #FF4400
	}

	.cart-lists {
		padding: 20rpx;
	}

	.cart-lists .item {
		display: flex;
		background-color: #fff;
		border-radius: 20rpx;
		padding: 20rpx;
		margin-bottom: 20rpx;


	}

	.cart-lists .item:last-child {
		border-bottom: none
	}

	.cart-lists .item .thumb {
		width: 120rpx;
		height: 120rpx;
		border-radius: 8rpx;
		margin-right: 20rpx;
	}

	.cart-lists .item .info {
		flex: 1;
		margin-top: 0;
		display: flex;
		flex-direction: column;
	}

	.cart-lists .item .info .title {
		font-size: 32rpx;
		display: flex;
		justify-content: space-between;
		margin-bottom: 20rpx;
		font-weight: bold;
	}

	.cart-lists .item .info .title text {
		font-size: 24rpx;
		color: #999;
		font-weight: normal;
	}

	.cart-lists .item .info .num {
		color: #F74437;
	}

	.comment-box {
		width: 80vw;
		background-color: #fff;
		border-radius: 20rpx;
		display: flex;
		flex-direction: column;
		padding: 20rpx;
	}

	.comment-box .title {
		text-align: center;
		font-size: 32rpx;
		font-weight: bold;
		margin: 20rpx 0;
	}

	.comment-box .star {
		margin: 10rpx 0;
	}

	.comment-box .textarea {
		margin: 10rpx 0;

	}

	.comment-box .upload {
		margin: 10rpx 0;
	}

	.comment-box .btn {
		background-color: #FF4400;
		height: 90rpx;
		border-radius: 16rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #FFF;
		margin-top: 10rpx;
	}

	.popup {
		background-color: #fff;
		width: 650rpx;
		border-radius: 24rpx;
		display: flex;
		flex-direction: column;
		overflow: hidden;

	}

	.popup .dl {
		margin: 30rpx 30rpx 0 30rpx;
	}

	.popup .dl .dt {
		color:#FF4400;
		font-size: 16px;
		margin-bottom: 20rpx;
	}

	.popup .dl .dd {
		color: #333;
		font-size: 14px;
		margin: 8rpx 0;
	}

	.popup .popup-title {
		text-align: center;
		margin: 30rpx 0;
		font-size: 36rpx;
	}

	.popup .info {
		text-align: right;
		font-size: 12px;
		color: #ff0;
		margin: 30rpx 30rpx 0 0;
	}

	.popup .btn {
		text-align: center;
		height: 90rpx;
		line-height: 90rpx;
		background-color: #FF4400;
		color: #fff;
		margin: 30rpx;
		border-radius: 16rpx;
	}

	.pictures {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		justify-content: space-between;
	}

	.pictures .item image {
		width: 230rpx;
		height: 230rpx;
	}
</style>
